.hero{position:relative;display:flex;align-items:center;justify-content:center;height:75vh;color:#fff;padding:30px 0;overflow:hidden}@media(min-width: 768px){.hero{padding:60px 0;height:60vh}}.hero__background{position:absolute;top:0;left:0;width:100%;height:100%;background:url("../../assets/bg-mobile.webp") center/cover no-repeat;background-attachment:fixed;filter:brightness(0.4)}@media(min-width: 768px){.hero__background{background:url("../../assets/bg-desktop.webp") center/cover no-repeat}}.hero__content{position:relative;display:flex;flex-direction:column;justify-content:center;text-align:center;align-items:center;padding:0 16%;max-height:100vh;overflow-y:auto;scroll-behavior:smooth;opacity:0;transform:translateY(20px);transition:opacity .6s ease,transform .6s ease}.hero__content.visible{opacity:1;transform:translateY(0)}@media(min-width: 768px){.hero__content{align-items:center;text-align:center}}@media(min-width: 1068px){.hero__content{gap:20px}}.hero__paragraph{margin:0;font-family:"Geometria Medium";font-size:16px;line-height:1.4em;padding-bottom:30px;text-align:left}@media(min-width: 768px){.hero__paragraph{font-family:"Geometria Medium";font-size:22px;line-height:1.4em}}.hero__button{display:inline-block;padding:12px 32px;background-color:#4ca89d;color:#fff;border-radius:8px;text-decoration:none;border:1px solid #fff;font-family:"Geometria Light";font-size:14px;font-weight:400;line-height:1.4em;letter-spacing:0}@media(min-width: 768px){.hero__button{font-family:"Geometria Light";font-size:16px;font-weight:400;line-height:1.4em}}.hero__button:hover{background-color:#fff;color:#4ca89d;letter-spacing:0;border:1px solid #4ca89d;transition:all .5s ease}.hero__wrapper{width:100%;display:flex;justify-content:flex-start}